/* footer 하단 고정 [S] */
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

#body-wrapper {
  min-height: 600px;
  position: relative;
}

#test.iranai {
  border-bottom: solid 1px;
  padding-bottom: 1rem;
}

.leftside {
  border-right: solid 1px;
  padding: 1rem;
}

#test3 {
  display: block;
  margin: 1rem 0;
  background-color: tomato;
}

.article {
  padding: 1rem;
}

#body-content {
  display: flex;
  max-width: 1100px;
  margin: 0 auto 0 auto;
}

#body-content.bk {
  margin: 84px auto 0 auto;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 250px; /* footer의 높이 */
  max-width: 1100px;
  display: flex;
}

footer {
  margin: 0 auto 0 auto;
  padding-left: 170px;
  padding-right: 170px;
  width: 100%;
  height: 200px; /* footer의 높이 */
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: black;
}
/* footer 하단 고정 [E] */



@media screen and (min-width: 625px) {

  #menu {
    display: flex;
    background-color: black;
  }

  #menu > a {
    padding: 20px 30px;
    font-size: 20px;
    color: white;
  }

  .fab {
    font-size: 35px;
    color: #1DA1F2;
  }

  .fas {
    display: none;
  }
}

@media screen and (max-width: 625px) {

  .nav {
    padding: 30px 40px;
  }

  #menu {
    display: none;
  }

  .fab {
    display : none;
  }

  .fas {
    font-size: 2em;
  }
}